@media screen and (max-width:450px){
    .dishes{
     flex-direction: column;
    
    }

    .row1 h2{
        font-size: 17px;
     
    }

    .row1 img{
       width: 150px;
    }


}


.app{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px;
    font-family: 'Baloo Bhai 2', cursive;
    flex-wrap: wrap;
}

.recepies{
    margin-top: 100px;
    /* border: 1px solid red; */
    display: flex;
    padding: 10px;
    flex-wrap: wrap;
    justify-content: space-around;
    grid-gap: 30px;
    gap: 30px;

}

h1{
    margin-bottom: 0;
}

span{
    margin: 0;
}

form{
    text-align: center;
}

form> input[type=text]{
    border: none;
    outline: none;
    width: 400px;
    background-color: white;
    padding: 0px 10px;
    height: 30px;
    cursor: pointer;
    border-radius: 10px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

form>input[type=submit]{
    width: 100px;
    border: none;
    outline: none;
    height: 25px;
    margin-top: 20px;
    background-color: rgb(91, 216, 91);
    cursor: pointer;
    font-size: 15px;

}

form>input[type=submit]:hover{
    background-color: white;
    color: green;
}
.Footerdiv{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 50px 0;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    /* border: 1px solid red; */
    width: 100%;
}

.innerfooterdiv{
    /* border: 1px solid red; */
    display: flex;
    width: 100%;
    /* max-width: 1024px; */
    justify-content: space-around;
    align-items: center;

  
}

.footeritems{
    display: flex;
    flex-direction: column  ;
    font-size: 15px;
}

.footeritems>h2{
    font-size: 40px;
    font-weight: bold;
    margin: 0;
}

.weekly{
    font-weight: 500;
    font-size: 20px;
    color: white;
}

.footeritems>input{
    box-sizing: border-box;
    border: none;
    outline: none;
    width: 300px;
    height: 30px;
    padding: 0 30px;
}
.footeritems>button{
    width: 100px;
    border: none;
    outline: none;
    height: 20px;
    align-self: center;
    margin-top: 10px;
    background-color: rgb(247, 182, 63);
    font-size: 14px;
    font-weight: bolder;
}

.Footerdiv>span{
    font-size: 13px;
    
}

@media screen and (max-width:425px) {

    .footeritem2{
        display: none;
       
        

    }

    .innerfooterdiv{
        justify-content: space-evenly;
    }

    .Footerdiv>span{
        text-align: center;
    }
    .maillistitem2 input{
