*{
    box-sizing: border-box;
 
}

.recepie_Card{
        /* border: 1px solid greenyellow; */
    background: linear-gradient(to bottom , #9f5daf, #ca9532);
    max-width: 500px;
    width: 100%;
    /* height: 300px; */
    padding: 10px 30px ;
    border-radius: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    box-shadow: 10px 15px 15px 0.5px rgb(53, 48, 48);
    /* padding: 10px; */
    position: relative;
   justify-content: space-between;
   gap: 30px;

}

.recepie_Card>button{
    align-self: center;
    width: 200px;
    border: none;
    outline: none;
    height: 30px;
    border-radius: 4px;
    background-color:  #9f5daf;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
}


.row1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    /* border: 1px solid    slateblue ; */
}
.row1 h2{
    margin: 0;
    font-size: 25px;
}
.row1 div{
    /* border: 1px solid red; */
    display: flex;
    flex-direction: column;
    

}

.row1 span{
    font-size: 15px;
}

.row1 img{
    width: 100%;
    max-width: 200px;
    border-radius: 50%;
    object-fit: cover   ;
    /* border: 1px solid red; */
    
}

.extra_info{
    /* border: 1px solid red; */
    width: 150px;
    height: 100px;
    display: flex;
    justify-content: space-around;
    color: rgb(34, 29, 29);
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-size: 15px;

}

.extra_info li{
    /* border: 1px solid red; */
    width: 100%;
    color: rgb(53, 36, 36);
    
}

.modal{
    position: absolute;
    left: 50%;
    bottom: 20%;
    transform: translateX(-50%);
    width: 400px;
    border-radius: 15px;

    background: linear-gradient(to bottom, #ca9532 , #9f5daf);
}

.modal p{
    /* border: 1px solid red; */
    padding: 20px;
   
    /* height: 300px; */
    
    flex-wrap: wrap;
    gap: 10px;
 
}


