.Footerdiv{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 50px 0;
    flex-direction: column;
    gap: 20px;
    /* border: 1px solid red; */
    width: 100%;
}

.innerfooterdiv{
    /* border: 1px solid red; */
    display: flex;
    width: 100%;
    /* max-width: 1024px; */
    justify-content: space-around;
    align-items: center;

  
}

.footeritems{
    display: flex;
    flex-direction: column  ;
    font-size: 15px;
}

.footeritems>h2{
    font-size: 40px;
    font-weight: bold;
    margin: 0;
}

.weekly{
    font-weight: 500;
    font-size: 20px;
    color: white;
}

.footeritems>input{
    box-sizing: border-box;
    border: none;
    outline: none;
    width: 300px;
    height: 30px;
    padding: 0 30px;
}
.footeritems>button{
    width: 100px;
    border: none;
    outline: none;
    height: 20px;
    align-self: center;
    margin-top: 10px;
    background-color: rgb(247, 182, 63);
    font-size: 14px;
    font-weight: bolder;
}

.Footerdiv>span{
    font-size: 13px;
    
}

@media screen and (max-width:425px) {

    .footeritem2{
        display: none;
       
        

    }

    .innerfooterdiv{
        justify-content: space-evenly;
    }

    .Footerdiv>span{
        text-align: center;
    }
    .maillistitem2 input{
        width: 150px;
    }
}