.app{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-family: 'Baloo Bhai 2', cursive;
    flex-wrap: wrap;
}

.recepies{
    margin-top: 100px;
    /* border: 1px solid red; */
    display: flex;
    padding: 10px;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 30px;

}

h1{
    margin-bottom: 0;
}

span{
    margin: 0;
}

form{
    text-align: center;
}

form> input[type=text]{
    border: none;
    outline: none;
    width: 400px;
    background-color: white;
    padding: 0px 10px;
    height: 30px;
    cursor: pointer;
    border-radius: 10px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

form>input[type=submit]{
    width: 100px;
    border: none;
    outline: none;
    height: 25px;
    margin-top: 20px;
    background-color: rgb(91, 216, 91);
    cursor: pointer;
    font-size: 15px;

}

form>input[type=submit]:hover{
    background-color: white;
    color: green;
}